<template>
	<ElForm
		ref="ruleFormRef"
		:model="form"
		label-position="right"
		label-width="100px"
		size="large"
		class="login-basic-form"
	>
		<ElFormItem
			label="网站地址："
			prop="url"
			:rules="[{ required: true, message: '网站地址不能为空' }]"
		>
			<div class="w-full flex items-center justify-between">
				<el-input class="mr-1" v-model="form.url" placeholder="请输入网站地址" clearable>
				</el-input>
				<el-tooltip content="抓取到的信息可自动填入表单" placement="top">
					<el-button
						:loading="getWebsiteInfoLoading"
						type="warning"
						@click="handleGetWebsiteInfo(form.url)"
						:disabled="!form.url"
					>
						抓取信息
					</el-button>
				</el-tooltip>
			</div>
		</ElFormItem>
		<ElFormItem
			label="网站名称："
			prop="name"
			:rules="[{ required: true, message: '网站名称不能为空' }]"
		>
			<el-input
				v-model="form.name"
				placeholder="请输入网站名称"
				clearable
				@change="getCustomerToImage"
			/>
		</ElFormItem>
    <ElFormItem label="保存位置：" prop="pid" :rules="[{ required: true, message: '位置不能为空' }]">
      <el-tree-select v-model="form.pid" placeholder="请选择位置" :data="treeDatas" empty-text="无匹配数据" check-strictly filterable default-expand-all>
        <template #default="{ node, data }">
          <div class="custom-tree-node w-full flex items-center justify-between">
					<span class="flex items-center">
						<SvgIcon
              :name="getIcon(data)"
              :size="18"
              class="mr-1"
            />
						<span>{{ node.label }}</span>
          </span>
          </div>
        </template>
      </el-tree-select>
    </ElFormItem>
		<ElFormItem label="网站logo：">
			<el-radio-group v-model="logoRadioValue" @change="handleRadioChange">
				<div class="logo-image mr-6">
					<!-- <img :src="form.logoUrl" v-if="form.logoUrl" /> -->
					<el-upload
						v-loading="uploadLoading"
						class="avatar-uploader"
						:action="action"
						:show-file-list="false"
						accept=".jpg,.jpeg,.png,.JPG,.JPEG"
						:on-success="handleUploadSuccess"
						:before-upload="beforeUpload"
					>
						<img v-if="form.logoUrl" :src="logoUrl" />
						<div class="logo-upload" v-else>点击上传</div>
					</el-upload>
					<div class="logo-radio text-center">
						<el-radio :label="1">{{ '' }}</el-radio>
					</div>
				</div>
				<div class="logo-image relative">
					<div class="custome-logo">
						<span
							ref="customeLogoRef"
							class="custome-word"
							:style="`background-color: ${randomColor};z-index:${customeLogoUrl ? '-10' : '10'}`"
							>{{ customLogoName }}</span
						>
						<img :src="customeLogoUrl" />
					</div>
					<span class="color-picker">
						<el-color-picker
							size="small"
							v-model="color"
							show-alpha
							:predefine="predefineColors"
							@change="handleColorChange"
						/>
					</span>
					<div class="logo-radio text-center">
						<el-radio :label="2">{{ '' }}</el-radio>
					</div>
				</div>
			</el-radio-group>
		</ElFormItem>
		<ElFormItem label="网站描述：" prop="desc">
			<el-input
				v-model="form.desc"
				type="textarea"
				placeholder="请输入网站描述，最多255个字符"
				clearable
				:rows="4"
				maxlength="255"
				show-word-limit
			/>
		</ElFormItem>
		<div class="flex justify-end mt-10">
			<el-button type="primary" @click="handleSure">确定</el-button>
			<el-button @click="handleClose">取消</el-button>
		</div>
	</ElForm>
</template>

<script setup lang="ts">
	import { fetchSaveWebsite, fetchUploadImage, fetchWebsiteInfo } from '@/api/fetchs/files'
	import { colorToHex, getRandom, mixSubstring } from '@/utils'
	import { ElUpload, ElColorPicker, ElMessage } from 'element-plus'
  import {ref, watchEffect, computed, onMounted, nextTick, unref} from 'vue'
	import { WebsiteForm } from './useWebsiteForm'
	import { HTTPS_KEY } from '@/enums/keyEnum'
	import { getSearchParams } from '@/utils/form'
	import { useEmit } from '@/utils/useEmitter'
	import env from '@/utils/env/env'
	import { useConst } from '../const'
	import html2canvas from 'html2canvas'
	import { base64ToBlob, blobToFile, checkUrl } from '@/utils/file'
  import {useFilesStore} from "@/store/modules/files";
  import {fetchDirTree} from "@/api/fetchs/teamNav";
  import {useTeamStore} from "@/store/modules/team";

	const props = defineProps<{
		defaultForm?: WebsiteForm
	}>()
  const filesStore = useFilesStore()
  const teamStore = useTeamStore()
	const ruleFormRef = ref()
	const logoRadioValue = ref<number>(1)
	const form = ref<WebsiteForm>({
		url: '',
		name: '',
		logoUrl: '',
		desc: '',
		pid: '',
    spaceId: '',
	})
	const action = `${env.publicApi}/api/v1/file/uploadUserWebsiteLogo`
	const logoUrl = ref<string | undefined>('')
	const html2canvasImage = ref<any>('')
	const customeLogoRef = ref()
	const customeLogoUrl = ref<string>('')

	const uploadLoading = ref<boolean>(false)
	const getWebsiteInfoLoading = ref<boolean>(false)
	const { predefineColors } = useConst()
	const randomColor = ref<string>(getRandomColor())
	const color = ref<string>(randomColor.value)
  const breadcrumbList = computed<any>(() => filesStore.getFolderBreadcrumb)
  const treeDatas = ref<Recordable[]>([])

	watchEffect(() => {
		if (props.defaultForm) {
			const { logo, ...rest } = props.defaultForm
			form.value = Object.assign({}, { logoUrl: logo || '', ...rest })
			logoUrl.value = logo
		} else {
			form.value = {
				url: HTTPS_KEY,
				name: '',
				logoUrl: '',
				desc: '',
				pid: ''
			}
		}
	})

	const customLogoName = computed(() =>
		form.value.name ? mixSubstring(form.value.name, 3) : form.value.name
	)
	const emit = defineEmits(['formResponse'])

  function getIcon(data) {
    if(data.dirId === 'root') {
      return data.spaceType == 'MINE' ? 'menu-self-file' : data.spaceType == 'PUBLIC' ? 'space-public' : 'space-private'
    }else {
      return 'list-folder'
    }
  }
	function handleColorChange() {
		randomColor.value = color.value
		getCustomerToImage()
	}
	function handleRadioChange() {
		// if (val === 2) {
		// 	// form.value.logoUrl = JSON.stringify({
		// 	// 	bg: randomColor.value.slice(1, randomColor.value.length)
		// 	// 	// name: unref(customLogoName)
		// 	// })
		// 	form.value.logoUrl = `/cccustomebackground/${randomColor.value}`
		// }
	}
	function getCustomerToImage() {
		html2canvas(customeLogoRef.value, {
			useCORS: true,
			scale: 1
		}).then(canvas => {
			let url = canvas.toDataURL('image/png')
			console.log('url:', url)
			let blob = base64ToBlob(url)
			const file = blobToFile(blob, 'customer-logo.png')
			html2canvasImage.value = file
			customeLogoUrl.value = url
		})
	}
	onMounted(() => {
		nextTick(() => {
			getCustomerToImage()
		})
	})

	function handleSure() {
		ruleFormRef.value.validate(valid => {
			if (valid) {
				handleSaveWebsiteApi()
				emit('formResponse', true)
			}
		})
	}
	function handleClose() {
		emit('formResponse', false)
	}
	function getRandomColor() {
		const c1 = getRandom(0, 255)
		const c2 = getRandom(0, 255)
		const c3 = getRandom(0, 255)
		return colorToHex(`rgb(${c1},${c2},${c3})`)
	}
	function handleUploadSuccess(res) {
		logoUrl.value = res.data
		form.value.logoUrl = res.data
		uploadLoading.value = false
	}
	function beforeUpload(file) {
		uploadLoading.value = true
		const isLt2M = file.size / 1024 / 1024 < 2
		if (!isLt2M) {
			// @ts-ignore
			ElMessage.error('图片不能超过2MB!')
		}
		return isLt2M
	}
  // 定义递归函数（判断value是否在树中）
  function existElement(treeDatas, value) {
    for (let i = 0; i < treeDatas.length; i++) {
      const element = treeDatas[i];
      if(element.value === value) {
        return true
      }

      if (element.children && element.children.length > 0) {
        if(existElement(element.children, value)) {
          return true
        }
      }
    }

    return false;
  }
	/* ================================== fetchs ================================== */
	async function handleSaveWebsiteApi() {
		if (logoRadioValue.value === 2) {
			const formData = new FormData()
			formData.append('file', html2canvasImage.value)
			const { data } = await fetchUploadImage(formData)
			form.value.logoUrl = data
		} else {
			form.value.logoUrl = logoUrl.value
		}
    let p = form.value
    if(p.pid?.startsWith('root:')) {
      p.spaceId = p.pid?.substring(5)
      p.pid = 'root'
    }
		const params = getSearchParams(p)
		const {data} = await fetchSaveWebsite(params)
    if(data) {
      ElMessage.success('保存成功')
    }
		useEmit('handleUpdateList')
	}
	async function handleGetWebsiteInfo(url) {
    while(url.endsWith('/')) {
      url = url.slice(0, url.length - 1)
    }
		if (!checkUrl(url)) {
			ElMessage.warning('请输入有效的网站地址')
			return
		}
		getWebsiteInfoLoading.value = true
		const { data } = await fetchWebsiteInfo(url)
		if (data) {
			const { title, description } = data
			form.value.name = title
			form.value.logoUrl = data.logoUrl
			logoUrl.value = data.logoUrl
			form.value.desc = description
			setTimeout(() => {
				getCustomerToImage()
			}, 200)
		} else {
			ElMessage.warning('未抓取到信息，请确保网站地址正确有效')
		}
		setTimeout(() => {
			getWebsiteInfoLoading.value = false
		}, 500)
	}
  async function handleGetDirTree() {
    const params = {teamId: teamStore.getActiveTeamId}
    const {data} = await fetchDirTree(params)
    treeDatas.value = data

    let pid = unref(breadcrumbList)[unref(breadcrumbList).length - 1].id
    if(!existElement(treeDatas.value, pid)) {
      pid = treeDatas.value[0].value
    }
    form.value.pid = pid
  }

  handleGetDirTree()
	// 上传服务器
	// function sendUrl() {
	// 	setTimeout(async () => {
	// 		customeLoading.value = true
	// 		const formData = new FormData()
	// 		formData.append('file', html2canvasImage.value)
	// 		const { data } = await fetchUploadImage(formData)
	// 		customeLogoUrl.value = data
	// 		customeLoading.value = false
	// 	}, 500)
	// }
</script>
<style lang="scss" scoped>
	$--width: 70px;
	.logo-image {
		position: relative;
		img {
			width: $--width;
			height: $--width;
			border: 1px solid #fff;
			border-radius: 4px;
		}
		.avatar-uploader {
			width: $--width;
			height: $--width;
		}
		.logo-upload {
			text-align: center;
			border-radius: 4px;
			width: $--width;
			height: $--width;
			line-height: $--width;
			font-size: 13px;
			cursor: pointer;
			background-color: #efefed;
		}
		.custome-logo {
			position: relative;
			.custome-word {
				position: absolute;
				top: 0;
				left: 0;
				z-index: 10;
				width: $--width;
				height: $--width;
				line-height: 50px; //文字偏移问题。。。
				text-align: center;
				color: #ffffff;
				font-weight: bolder;
				border-radius: 10px;
				overflow: hidden;
				display: block;
				align-items: center;
				justify-content: center;
				font-size: 22px;
				white-space: nowrap;
			}
		}
	}
	.color-picker {
		position: absolute;
		right: -27px;
		bottom: 46px;
		z-index: 99;
		display: flex;
		align-items: center;
		//.el-button {
		//	margin-left: 10px;
		//}
	}
	.logo-radio {
		.el-radio__label {
			padding: 0;
		}
	}
  .el-select {
    width: 100%;
