import { computed, unref, onUnmounted, ref } from 'vue'
import { useFilesStore } from '@/store/modules/files'
import { DataTypeEnum } from './types'
import { useEmit } from '@/utils/useEmitter'
import { fetchMove, fetchSaveSort } from '@/api/fetchs/files'
import { avgSortNum } from '@/utils'
import { getSearchParams } from '@/utils/form'
import { useLocalStorage } from '@/utils/cache/index'
import { SORT_TYPE_KET } from '@/enums/keyEnum'
import { SortTypeEnum } from '@/api/model/filesModel'

export function useMouseEvent() {
	const datalist = ref([])
	const filesStore = useFilesStore()
	const getStartItem = computed(() => filesStore.getStartItem)
	const getEnterId = computed(() => filesStore.getEnterId)
	const getPointOffset = computed(() => filesStore.getPointOffset)
	const getEnterType = computed(() => filesStore.enterType)
	const getBreadcrumbs = computed(() => filesStore.getFolderBreadcrumb)
	const getIsClick = computed(() => filesStore.getIsClick)
	const moveFlag = ref(false)
	const firstTime = ref()

	function handleMousedown(item, ev, list?) {
		console.log('ev:', ev)
		const sortType = useLocalStorage.get(SORT_TYPE_KET)
		if (sortType !== SortTypeEnum.CUSTOM || ev.button === 2) {
			return
		}
		firstTime.value = new Date().getTime()
		moveFlag.value = false
		datalist.value = list
		filesStore.setStartItem(item)
		filesStore.setIsClick(true)
		// console.log('start:', unref(getStartItem), unref(getIsClick))
		const { clientY } = ev
		document.onmousemove = e => {
			if (Math.abs(e.clientY - clientY) > 80) {
				filesStore.setIsClick(false)
			}
			filesStore.setPointOffset({ x: e.clientX || -999, y: e.clientY || -999 })
		}
		document.addEventListener('mouseup', handleApi, true)
	}
	function handleMouseenter(item) {
		if (
			unref(getStartItem).dataType === DataTypeEnum.FOLDER &&
			item.dataType === DataTypeEnum.WEBSITE
		) {
			filesStore.setEnterId('')
		} else {
			if (unref(getStartItem).id && item) {
				filesStore.setEnterId(item.id)
			} else {
				filesStore.setEnterId('')
			}
		}
	}
  // @ts-ignore
	function handleMouseleave(item) {
		if (unref(getStartItem).id === unref(getEnterId)) {
			return false
		}
		filesStore.setEnterId('')
	}
  // @ts-ignore
	function handleMousemove(item, e, type?) {
		filesStore.setIsClick(false)
		if (unref(getStartItem).id) {
			filesStore.setEnterType(type)
		}
		if (
			unref(getStartItem).dataType === DataTypeEnum.FOLDER &&
			item.dataType === DataTypeEnum.WEBSITE
		) {
			filesStore.setEnterId('')
		} else {
			if (item && unref(getStartItem)) {
				filesStore.setEnterId(item.id)
			} else {
				filesStore.setEnterId('')
			}
		}
	}
	function handleMouseup() {
		// console.log('item.name:', item.name, filesStore.getStartItem)
		// const form = {
		// 	start_id: unref(getStartItem).id,
		// 	to_id: item.id
		// }
		// console.log('form:', form)
		// handleSaveMoveApi(form)
		// handleSaveMoveApi()
	}
	function reset() {
		// console.log('reset..')
		filesStore.setIsClick(true)
		filesStore.setEnterId('')
		filesStore.setStartItem({})
		filesStore.setEnterType('')
	}

	/* ================================== fetchs ================================== */

	function handleApi(e) {
		console.log('up---', e)
		const lastBread = unref(getBreadcrumbs)[unref(getBreadcrumbs).length - 1]
		if (unref(getEnterId) !== lastBread.id) {
			if (unref(getEnterType) === 'interspace') {
				handleSaveSortApi()
			} else {
				handleSaveMoveApi()
			}
		}
		reset()
		document.removeEventListener('mouseup', handleApi, true)
	}

	async function handleSaveSortApi() {
		if (unref(getEnterId) && unref(getEnterId) !== unref(getStartItem).id) {
			const form = {
				id: unref(getStartItem).id,
				sortNum: avgSortNum(unref(getEnterId), datalist.value)
			}
			console.log('form:', form)
			await fetchSaveSort(getSearchParams(form))
			// ElMessage.success('排序成功')
			document.onmousemove = null
			useEmit('handleUpdateList')
		}
	}

	async function handleSaveMoveApi() {
		console.log('up..', unref(getEnterId))
		if (unref(getEnterId) && unref(getEnterId) !== unref(getStartItem).id) {
			const form = {
				dataIds: [unref(getStartItem).id],
				folderId: unref(getEnterId)
			}
			console.log('form:', form)
			await fetchMove(form)
			// ElMessage.success('移动成功')
			document.onmousemove = null
			useEmit('handleUpdateList')
		}
	}

	onUnmounted(() => {
		document.removeEventListener('mouseup', handleApi, true)
	})

	return {
		getStartItem,
		getEnterId,
		getPointOffset,
		getEnterType,
		getIsClick,
		handleMousedown,
		handleMouseenter,
		handleMouseleave,
		handleMousemove,
		handleMouseup
	}
}
