export function useWebsitePublish() {

  function handleOpenWebsiteDetail(websiteItem) {
    window.open(window.location.href + '/' + websiteItem.id, '_self')
  }

  function handleOpenWebsite(websiteItem) {
    window.open(websiteItem.url, '_blank')
  }

  return {
    handleOpenWebsiteDetail,
    handleOpenWebsite
  }
}
