<template>
  <div @click.stop>
    <Dropdown
      ref="listItemDropdown"
      trigger="hover"
      :dropMenuList="itemMenuList"
      :key="`${ikey}-iconmenu`"
      @menuEvent="handleDropdown"
    >
      <div class="item-pop-menu flex items-center justify-center">
        <SvgIcon name="handle-pop-menu" :size="18" />
      </div>
      <SimpleDialog v-model:visible="folderData.visible" :title="folderData.title" :footer="false">
        <FolderForm :default-form="itemData" @formResponse="handleFolderRes" key="edit-form" />
      </SimpleDialog>
      <SimpleDialog
        v-model:visible="websiteData.visible"
        width="600px"
        :title="websiteData.title"
        :footer="false"
      >
        <WebsiteForm :default-form="itemData" @formResponse="handleWebsiteRes" />
      </SimpleDialog>
      <SimpleDialog
        v-model:visible="cloneMoveData.visible"
        :title="cloneMoveData.title"
        :footer="false"
      >
        <CloneMove :dataIds="[itemData.id]" :showAllDir="cloneIsCollect" :isCollect="cloneIsCollect" @formResponse="handleCloneMoveRes" />
      </SimpleDialog>
    </Dropdown>
  </div>
</template>

<script setup lang="ts">
  import Dropdown from '@/components/Dropdown/Dropdown.vue'
  import { DropdownEnum } from '@/enums/dropdownEnum'
  import SimpleDialog from '@/components/SimpleDialog/SimpleDialog.vue'
  import FolderForm from './folder/FolderForm.vue'
  import WebsiteForm from './website/WebsiteForm.vue'
  import CloneMove from './clone-move/CloneMove.vue'
  import {DropMenu} from "@/components/Dropdown/useDropdown";
  import { useCloneMoveForm } from './clone-move/useCloneMove'
  import { useFolderForm } from './folder/useFolderForm'
  import { useWebsiteForm } from './website/useWebsiteForm'
  import {
    handleTrash,
    handleRestore,
    handleDeleteComp,
    handleRemoveCommonUse,
    handleRemoveCommonUsePublish
  } from './useDelete'
  import {useTeamStore} from "@/store/modules/team";
  import {fetchAddCommonUse, fetchAddCommonUsePublish, fetchAddOpenHistory} from "@/api/fetchs/teamNav";
  import {ElMessage} from "element-plus";
  import {getSearchParams} from "@/utils/form";
  import {useEmit} from "@/utils/useEmitter";
  import {useWebsitePublish} from "@/components/ContentElement/useWebsitePublish";
  import {useRoute} from "vue-router";
  import useClipboard from "vue-clipboard3";

	const props = defineProps<{
		itemData: any
		ikey: string
    itemMenuList: DropMenu[]
    cloneIsCollect?: boolean
	}>()

  const teamStore = useTeamStore()
  const route = useRoute()
  const { toClipboard } = useClipboard()

	const { folderData, handleFolderVisible, handleFolderRes } = useFolderForm()
	const { websiteData, handleWebsiteVisible, handleWebsiteRes } = useWebsiteForm()
	const { cloneMoveData, handleCloneMoveVisible, handleCloneMoveRes } = useCloneMoveForm(props.cloneIsCollect ? '收藏到' : '移动/复制到')
  const {handleOpenWebsiteDetail, handleOpenWebsite} = useWebsitePublish()

  async function handleDropdown(dropItem) {
		const ids = [props.itemData].map(v => v.id)
		switch (dropItem.key) {
			case DropdownEnum.EDIT_FOLDER:
				handleFolderVisible(dropItem.text, 'edit')
				break
			case DropdownEnum.EDIT_WEBSITE:
				handleWebsiteVisible(dropItem.text, 'edit')
				break
			case DropdownEnum.DELETE:
				handleTrash(ids, props.itemData.name)
				break
			case DropdownEnum.RESTORE:
				handleRestore(props.itemData)
				break
			case DropdownEnum.DELETE_COMP:
				handleDeleteComp(props.itemData)
				break
			case DropdownEnum.CLONE_MOVE:
				handleCloneMoveVisible()
				break
      case DropdownEnum.ADD_COMMON_USE:
        handleAddCommonUse(props.itemData)
        break
      case DropdownEnum.REMOVE_COMMON_USE:
        handleRemoveCommonUse(props.itemData, teamStore.getActiveTeamId)
        break
      case DropdownEnum.OPEN_WEBSITE_DETAIL:
        handleOpenWebsiteDetail(props.itemData)
        break
      case DropdownEnum.OPEN_WEBSITE:
        handleSaveOpenHistory(props.itemData.id)
        handleOpenWebsite(props.itemData)
        break
      case DropdownEnum.REMOVE_COMMON_USE_PUBLISH:
        let publishId = route.params.publishId.indexOf('#') > 0 ? route.params.publishId.slice(0, route.params.publishId.indexOf('#')) : route.params.publishId
        handleRemoveCommonUsePublish(props.itemData, publishId)
        break
      case DropdownEnum.ADD_COMMON_USE_PUBLISH:
        handleAddCommonUsePublish(props.itemData)
        break
      case DropdownEnum.SHARE_WEBSITE:
        let pId = route.params.publishId.indexOf('#') > 0 ? route.params.publishId.slice(0, route.params.publishId.indexOf('#')) : route.params.publishId
        let detailUrl = window.location.origin + '/p/' + pId + '/' + props.itemData.id
        let value = '网站名称：' + props.itemData.name + '\n网站地址：' + props.itemData.url + '\n网站详情：' + detailUrl
        await toClipboard(value)
        ElMessage.success('分享信息已复制')
        break
		}
	}

  /* ================================== fetchs ================================== */
  async function handleAddCommonUse(item) {
    const json = {navId: item.id, teamId: teamStore.getActiveTeamId}
    const param = getSearchParams(json)
    const {data} = await fetchAddCommonUse(param)
    if(data) {
      ElMessage.success('添加成功')
      useEmit('handleUpdateList')
    }
  }

  async function handleAddCommonUsePublish(item) {
    const publishId = route.params.publishId.indexOf('#') > 0 ? route.params.publishId.slice(0, route.params.publishId.indexOf('#')) : route.params.publishId
    const json = {navId: item.id, publishId}
    const param = getSearchParams(json)
    const {data} = await fetchAddCommonUsePublish(param)
    if(data) {
      ElMessage.success('添加成功')
      useEmit('handleUpdateTeamPublishDatas')
    }
  }

  async function handleSaveOpenHistory(navId) {
    const json = {navId}
    const param = getSearchParams(json)
    await fetchAddOpenHistory(param)
  }
</script>

<style lang="scss">
	.item-pop-menu {
		width: 24px;
		height: 24px;
		line-height: 24px;
		border-radius: 50%;
		text-align: center;
		background-color: #fff;
		box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.3);
	}
</style>
