import {reactive} from 'vue'
import {FileItem} from '../types'

export type FolderFormType = Partial<FileItem>

export function useFolderForm() {
  const folderData = reactive({
    visible: false,
    title: '新建',
    type: 'add'
  })

  function handleFolderVisible(text, type = 'add') {
    folderData.type = type
    folderData.title = text
    folderData.visible = true
  }

  // 文件夹操作
  function handleFolderRes(val) {
    if (val) {
      console.log('操作成功后...')
    }
    folderData.visible = false
  }

  return {
    folderData,
    handleFolderVisible,
    handleFolderRes
  }
}
