import { h } from 'vue'
import { fetchRestore, fetchTrash, fetchDeleteCompletely, fetchClearAll } from '@/api/fetchs/files'
import { useEmit } from '@/utils/useEmitter'
import { getSearchParams } from '@/utils/form'
import { ElMessage, ElMessageBox } from 'element-plus'
import {fetchRemoveCommonUse, fetchRemoveCommonUsePublish} from "@/api/fetchs/teamNav";

/* 移入回收站 */
export function handleTrash(idList, idText?: string) {
	ElMessageBox({
		title: '请确认',
		message: h('p', null, [
			//`您将把 ${idText} 移除到垃圾箱中，是否继续？`
			h('p', null, [
				h('span', null, '您将把 '),
				h('i', null, [h('b', null, `${idText}`)]),
				h('span', null, ' 移除到垃圾箱中，是否继续？')
			]),
			h('p', null, '删除后，您可以从回收站恢复。')
		]),
		showCancelButton: true,
		cancelButtonText: '取消',
		confirmButtonText: '确认',
		draggable: true,
		beforeClose: (action, instance, done) => {
			if (action === 'confirm') {
				instance.confirmButtonLoading = true
				instance.confirmButtonText = 'Loading...'
				setTimeout(() => {
					done()
					setTimeout(() => {
						instance.confirmButtonLoading = false
					}, 300)
				}, 300)
			} else {
				done()
			}
		}
	}).then(async () => {
		await fetchTrash({ ids: idList })
		// @ts-ignore
		ElMessage.success('移入回收站成功')
		useEmit('handleUpdateList')
		useEmit('handleUpdateMultipleList')
	})
}
