/* 清空回收站 */
export function handleClearAll() {
	// @ts-ignore
	ElMessageBox.confirm('您将要清空回收站内所有文件，清空后文件无法恢复！是否继续？', '请确认', {
		confirmButtonText: '确定',
		cancelButtonText: '取消',
		type: 'warning'
	}).then(async () => {
		await fetchClearAll()
		// @ts-ignore
		ElMessage.success('清除成功')
		useEmit('handleUpdateList')
	})
}
