/* 移除我的常用 */
export function handleRemoveCommonUse(item, teamId) {
  // @ts-ignore
  ElMessageBox.confirm('您确认要将此文件从我的常用中移除？', '请确认', {
    confirmButtonText: '确定',
    cancelButtonText: '取消',
    type: 'warning'
  }).then(async () => {
    const json = {navId: item.id, teamId}
    const param = getSearchParams(json)
    const {data} = await fetchRemoveCommonUse(param)
    if(data) {
      ElMessage.success('移除成功')
    }
    useEmit('handleUpdateList')
  })
}
