import {reactive} from 'vue'

export interface WebsiteForm {
  url: string
  name: string
  logoUrl?: string
  desc?: string
  pid?: string
  logo?: string
  spaceId?: string
}

export function useWebsiteForm() {
  const websiteData = reactive({
    visible: false,
    title: '添加网站',
    type: 'add'
  })

  function handleWebsiteVisible(text, type = 'add') {
    websiteData.type = type
    websiteData.title = text
    websiteData.visible = true
  }

  // 网站操作
  function handleWebsiteRes(val) {
    if (val) {
      console.log('操作成功后...')
    }
    websiteData.visible = false
  }

  return {
    websiteData,
    handleWebsiteVisible,
    handleWebsiteRes
  }
}
